<template>
  <div class="list">
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="商品名称">
          <el-input v-model="params.goods_name" placeholder="商品名称"></el-input>
        </el-form-item>
        <el-form-item label="商品编号">
          <el-input v-model="params.goods_number" placeholder="商品编号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
        <div v-if="id"></div>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="spilkeArr"
        v-loading="loading"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column align="center" type="index" label="序号"></el-table-column>
        <el-table-column align="center" prop="goodsId" label="商品编号"></el-table-column>
        <el-table-column align="center" prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column align="center" prop="goodsNumber" label="可售库存"></el-table-column>
        <el-table-column align="center" prop="surplusQuantity" label="秒杀可售库存"></el-table-column>
        <el-table-column align="center" prop="maxQuantity" label="每人限购"></el-table-column>
        <el-table-column align="center" prop="shopPrice" label="原价">
          <template v-slot="scope">
            {{ scope.row.shopPrice }}<br />
            (现金价:{{ scope.row.cashPrice }} + 积分支付上限:{{ scope.row.moneyLine }})
          </template>
        </el-table-column>
        <el-table-column align="center" prop="signupShopPrice" label="秒杀总价"></el-table-column>
        <el-table-column align="center" prop="signupCashPrice" label="秒杀现金价"></el-table-column>
        <el-table-column align="center" prop="signupMoneyLine" label="秒杀积分支付上限"></el-table-column>
        <el-table-column align="center" prop="createdAt" label="提交时间">
          <template slot-scope="scope">
            {{ utils.format_date(scope.row.createdAt) }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="操作">
          <template slot-scope="scope">
            <el-button @click="removeGoods(scope.row)" type="text" size="small">移除</el-button>
            <el-button @click="editGoods(scope.row)" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--        <el-pagination background @current-change="handleCurrentChange" :current-page.sync="currentPage"-->
    <!--                       :page-size="params.limit" layout="prev, pager, next, jumper"-->
    <!--                       :total="total"></el-pagination>-->
    <el-dialog
      title="编辑商品"
      :visible.sync="editView"
      width="40%"
      :v-loading="editLoading"
      :close-on-click-modal="closeModal"
    >
      <el-form :rules="rules" ref="ruleForm" :model="form" label-width="120px">
        <el-form-item label="商品名称">
          <el-input v-model.trim="form.goodsName" disabled></el-input>
        </el-form-item>
        <el-form-item label="商品编号">
          <el-input v-model.trim="form.goodsId" disabled></el-input>
        </el-form-item>
        <el-form-item label="秒杀价" prop="signupShopPrice">
          <el-input type="number" v-model.trim="form.signupShopPrice"></el-input>
        </el-form-item>
        <el-form-item label="现金价" prop="signupCashPrice">
          <el-input type="number" v-model.trim="form.signupCashPrice"></el-input>
        </el-form-item>
        <el-form-item label="积分支付上限" prop="signupMoneyLine">
          <el-input type="number" v-model.trim="form.signupMoneyLine"></el-input>
        </el-form-item>
        <el-form-item label="原价">
          <el-input v-model.trim="form.shopPrice" disabled></el-input>
        </el-form-item>
        <el-form-item label="现金价">
          <el-input v-model.trim="form.cashPrice" disabled></el-input>
        </el-form-item>
        <el-form-item label="积分支付上限">
          <el-input v-model.trim="form.moneyLine" disabled></el-input>
        </el-form-item>
        <el-form-item label="可售库存">
          <el-input v-model.trim="form.goodsNumber" disabled></el-input>
        </el-form-item>
        <el-form-item label="活动可售库存" prop="surplusQuantity">
          <el-input type="number" v-model.trim="form.surplusQuantity"></el-input>
        </el-form-item>
        <el-form-item label="每人限购" prop="maxQuantity">
          <el-input type="number" v-model.trim="form.maxQuantity"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit(form)" :loading="btnLoading">提交</el-button>
          <el-button @click="editView = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'spikeList',
  props: ['id'],
  data() {
    return {
      currentPage: 1,
      total: 0,
      params: {
        id: '',
        goods_name: '',
        goods_number: '',
        type: 1
        // limit: 10,
        // page: 1,
      },
      loading: false,
      spilkeArr: [],
      editView: false,
      editLoading: false,
      closeModal: false,
      form: {},
      btnLoading: false,
      signupId: '',
      rules: {
        signupShopPrice: [
          { required: true, message: '请填写秒杀价', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        signupCashPrice: [
          { required: true, message: '请填写现金价', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        signupMoneyLine: [
          { required: true, message: '请填写积分支付上限', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        surplusQuantity: [
          { required: true, message: '请填写活动可售库存', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // console.log(11)
    // console.log(this.id,'props传递过来的ID');
    this.params.id = this.id
    this.goodsList()
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
    },
    search() {
      this.currentPage = 1
      this.params.page = 1
      this.goodsList()
    },
    clear() {
      this.currentPage = 1
      this.params.page = 1
      this.params.goods_name = ''
      this.params.goods_number = ''
      this.goodsList()
    },
    goodsList() {
      this.loading = true
      this.$api.get(
        `hd/itzSecbuySignupGoods/signup/detail`,
        this.params,
        res => {
          // console.log(res);
          this.loading = false
          if (res.status == 200) {
            this.spilkeArr = res.data.goodsList
            this.signupId = res.data.signup.id
            this.$emit('info', res.data.signup)
          }
        },
        err => {
          this.loading = false
        }
      )
    },
    removeGoods(n) {
      this.$confirm('是否删除此条商品信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$api.delete(`hd/itzSecbuySignupGoods/signup/detail/${n.signupGoodsId}`, null, res => {
            if (res.status == 200) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.goodsList()
            }
          })
        })
        .catch(() => {})
    },
    editGoods(e) {
      // console.log(e)
      this.form = e
      this.$api.put(`hd/itzSecbuySignupGoods/goods/lock/${e.signupGoodsId}`, null, res => {
        // console.log(res)
        if (res.status == 200) {
          this.editView = true
          this.goodsList()
        }
      })
    },
    onSubmit(e) {
      // console.log(e)
      this.btnLoading = true
      let obj = {
        shopPrice: e.signupShopPrice, //秒杀价
        cashPrice: e.signupCashPrice, //现金价
        huanbiPrice: e.signupMoneyLine, //积分支付上限
        surplusQuantity: e.surplusQuantity, //活动可售库存
        goodsId: e.goodsId,
        signupId: this.signupId,
        maxQuantity: e.maxQuantity
      }
      this.$api.put(
        `hd/itzSecbuySignupGoods/goods/${e.signupGoodsId}`,
        obj,
        res => {
          // console.log(res)
          if (res.status == 200) {
            this.$message({
              type: 'success',
              message: res.message
            })
            this.editView = false
            this.btnLoading = false
            this.goodsList()
          }
        },
        err => {
          this.btnLoading = false
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.list {
  margin-top: 10px;
}
</style>
