<template>
  <div class="success">
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="商品名称">
          <el-input v-model="params.goods_name" placeholder="商品名称"></el-input>
        </el-form-item>
        <el-form-item label="商品编号">
          <el-input v-model="params.goods_number" placeholder="商品编号"></el-input>
        </el-form-item>
        <el-form-item label="活动名称">
          <el-input v-model="params.secbuy_name" placeholder="活动名称"></el-input>
        </el-form-item>
        <el-form-item label="活动状态">
          <el-select v-model="params.secbuy_status" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
        <div v-if="id"></div>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="spilkeArr"
        v-loading="loading"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column align="center" type="index" label="序号"></el-table-column>
        <el-table-column align="center" prop="goodsId" label="商品编号"></el-table-column>
        <el-table-column align="center" prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column align="center" prop="goodsNumber" label="可售库存"></el-table-column>
        <el-table-column align="center" prop="surplusQuantity" label="秒杀库存"></el-table-column>
        <el-table-column align="center" prop="maxQuantity" label="每人限购"></el-table-column>
        <el-table-column align="center" prop="cashPrice" label="原价">
          <template v-slot="scope">
            {{ scope.row.shopPrice }}<br />
            (现金价:{{ scope.row.cashPrice }} + 积分支付上限:{{ scope.row.moneyLine }})
          </template>
        </el-table-column>
        <el-table-column align="center" prop="signupShopPrice" label="秒杀总价">
          <template v-slot="scope">
            {{ scope.row.signupShopPrice }}<br />
            (现金价:{{ scope.row.signupCashPrice }} + 积分支付上限:{{ scope.row.signupMoneyLine }})
          </template>
        </el-table-column>
        <el-table-column align="center" prop="secbuyName" label="活动名称"></el-table-column>
        <el-table-column align="center" label="活动时间">
          <template slot-scope="scope">
            {{ utils.format_date(scope.row.secbuyStartTime) }}<br />
            至 <br />{{ utils.format_date(scope.row.secbuyEndTime) }}
          </template>
        </el-table-column>
        <el-table-column align="center" prop="secbuyStatus_text" label="活动状态"></el-table-column>
        <el-table-column align="center" label="活动创建时间">
          <template slot-scope="scope">
            {{ utils.format_date(scope.row.createdAt) }}
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--        <el-pagination background @current-change="handleCurrentChange" :current-page.sync="currentPage"-->
    <!--                       :page-size="params.limit" layout="prev, pager, next, jumper"-->
    <!--                       :total="total"></el-pagination>-->
  </div>
</template>

<script>
export default {
  name: 'successSpikeList',
  props: ['id'],
  data() {
    return {
      currentPage: 1,
      total: 0,
      params: {
        id: '',
        goods_name: '',
        goods_number: '',
        secbuy_name: '',
        secbuy_status: '',
        type: 2
        // limit: 10,
        // page: 1,
      },
      loading: false,
      options: [
        {
          value: '0',
          label: '未开始'
        },
        {
          value: '1',
          label: '进行中'
        },
        {
          value: '2',
          label: '已结束'
        },
        {
          value: '3',
          label: '已禁用'
        }
      ],
      spilkeArr: []
    }
  },
  created() {
    // console.log(222);
    this.params.id = this.id
    this.goodsList()
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.params.pageNum = currentPage
    },
    search() {
      this.currentPage = 1
      this.goodsList()
    },
    clear() {
      this.currentPage = 1
      this.params.goods_name = ''
      this.params.goods_number = ''
      this.params.secbuy_name = ''
      this.params.secbuy_status = ''
      this.goodsList()
    },
    goodsList() {
      this.loading = true
      this.$api.get(
        `hd/itzSecbuySignupGoods/signup/detail`,
        this.params,
        res => {
          // console.log(res);
          this.loading = false
          if (res.status == 200) {
            for (let i of res.data.goodsList) {
              switch (i.secbuyStatus) {
                case 0:
                  i.secbuyStatus_text = '未开始'
                  break
                case 1:
                  i.secbuyStatus_text = '进行中'
                  break
                case 2:
                  i.secbuyStatus_text = '已结束'
                  break
                case 3:
                  i.secbuyStatus_text = '禁用'
                  break
                default:
                  i.secbuyStatus_text = ''
              }
            }
            this.spilkeArr = res.data.goodsList
          }
        },
        err => {
          this.loading = false
        }
      )
    },
    removeGoods() {},
    editGoods() {}
  }
}
</script>

<style scoped>
.el-pagination {
  text-align: center;
  margin-top: 10px;
}

.success {
  margin-top: 10px;
}
</style>
