<template>
  <div class="details">
    <page-title first-title="秒杀活动报名" second-title="报名详情"></page-title>
    <div class="btn-box">
      <el-button
        v-if="mark == 'signUp'"
        type="primary"
        size="medium"
        style="float: right;"
        @click="dialogVisible = true"
        >提交商品</el-button
      >
    </div>
    <div class="button-wrapper">
      <div class="title">
        <div class="title-name">
          <span>报名主题：</span>
          <span class="rear">{{ form.secName }}</span>
        </div>
        <div class="title-name">
          <span>支持类目：</span>
          <span class="rear">{{ form.categoryId }}</span>
        </div>
        <div class="title-name">
          <span>报名时间：</span>
          <span class="rear">{{ utils.format_date(form.startTime) }}</span>
          <span style="margin: 0 10px;">至</span>
          <span class="rear">{{ utils.format_date(form.endTime) }}</span>
        </div>
      </div>
      <div class="title-below">
        <div class="below-title">主题说明：</div>
        <div class="below-rear">{{ form.secDesc }}</div>
      </div>
    </div>
    <p class="border"></p>
    <el-tabs type="border-card">
      <el-tab-pane label="已提交">
        <spikeListView :id.sync="id" @info="goodsInfo"></spikeListView>
      </el-tab-pane>
      <el-tab-pane label="报名成功">
        <successSpikeListView :id.sync="id"></successSpikeListView>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      title="提交商品"
      :visible.sync="dialogVisible"
      width="80%"
      :v-loading="dialogLoading"
      :close-on-click-modal="closeModal"
    >
      <template>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="全部" name="All">
            <div class="goods">
              <div class="search-wrapper" v-show="tableIndex == 0">
                <el-form :inline="true" class="demo-form-inline">
                  <el-form-item label="商品名称">
                    <el-input v-model="params.goodsName" placeholder="商品名称"></el-input>
                  </el-form-item>
                  <el-form-item label="商品编号">
                    <el-input v-model="params.goodsId" placeholder="商品编号"></el-input>
                  </el-form-item>
                  <el-form-item label="商品分类">
                    <el-select v-model="value" multiple placeholder="请选择">
                      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
                    <el-button type="primary" @click="clear" icon="el-icon-refresh">清空</el-button>
                  </el-form-item>
                </el-form>
              </div>
              <div class="list-wrapper">
                <el-table
                  :data="goodsListArr"
                  :row-key="getRowKey"
                  v-loading="loading"
                  stripe
                  style="width: 100%"
                  :header-cell-style="{ background: '#eef1f6' }"
                  @selection-change="selectCall"
                  ref="multipleTable"
                >
                  <el-table-column type="selection" :reserve-selection="isReserve" width="55"></el-table-column>
                  <el-table-column align="center" prop="goodsId" label="商品编号"></el-table-column>
                  <el-table-column align="center" prop="goodsName" label="商品名称"></el-table-column>
                  <el-table-column align="center" prop="goodsNumber" label="可售库存"></el-table-column>
                  <el-table-column align="center" prop="surplusQuantity" label="秒杀可售库存">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.saleQuantity[scope.row.goodsId]"
                        placeholder="秒杀可售库存"
                      ></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="maxQuantity" label="每人限购">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.maxQuantity[scope.row.goodsId]"
                        placeholder="每人限购"
                      ></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="shopPrice" label="原价">
                    <template v-slot="scope">
                      {{ scope.row.shopPrice }}<br />
                      (现金价:{{ scope.row.cashPrice }} + 积分支付上限:{{ scope.row.moneyLine }})
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="signupShopPrice" label="秒杀总价">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.shopPrice[scope.row.goodsId]"
                        placeholder="秒杀总价"
                        @change="inputshopPrice(scope.row.goodsId)"
                      ></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="signupCashPrice" label="秒杀现金价">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.cashPrice[scope.row.goodsId]"
                        placeholder="秒杀现金价"
                        @change="inputCashPrice(scope.row.goodsId)"
                      ></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="signupMoneyLine" label="秒杀积分支付上限">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.huanbiPrice[scope.row.goodsId]"
                        placeholder="秒杀积分支付上限"
                        disabled
                      ></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="signupMoneyLine" label="每日限购数量">
                    <template v-slot="scope">
                      <el-input
                        v-model.trim="objSplike.maxQuantity[scope.row.goodsId]"
                        placeholder="每日限购数量"
                        disabled
                      ></el-input>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <el-pagination
                background
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="params.pageSize"
                layout="prev, pager, next, jumper"
                :total="total"
              ></el-pagination>
            </div>
          </el-tab-pane>
          <el-tab-pane :label="title" name="selected">
            <div class="goods">
              <div class="list-wrapper">
                <el-table
                  :data="goodsSpikeArr"
                  :row-key="getRowKeyObj"
                  v-loading="loadingObj"
                  stripe
                  style="width: 100%"
                  :header-cell-style="{ background: '#eef1f6' }"
                  @selection-change="selectCallObj"
                  ref="objTable"
                >
                  <el-table-column type="selection" :reserve-selection="isReserveObj" width="55"></el-table-column>
                  <el-table-column align="center" prop="goodsId" label="商品编号"></el-table-column>
                  <el-table-column align="center" prop="goodsName" label="商品名称"></el-table-column>
                  <el-table-column align="center" prop="goodsNumber" label="可售库存"></el-table-column>
                  <el-table-column align="center" prop="surplusQuantity" label="秒杀可售库存"> </el-table-column>
                  <el-table-column align="center" prop="maxQuantity" label="每人限购"> </el-table-column>
                  <el-table-column align="center" prop="shopPrice" label="原价">
                    <template v-slot="scope">
                      {{ scope.row.shopPrice }}<br />
                      (现金价:{{ scope.row.cashPrice }} + 积分支付上限:{{ scope.row.moneyLine }})
                    </template>
                  </el-table-column>
                  <el-table-column align="center" prop="signupShopPrice" label="秒杀总价"> </el-table-column>
                  <el-table-column align="center" prop="signupCashPrice" label="秒杀现金价"> </el-table-column>
                  <el-table-column align="center" prop="signupMoneyLine" label="秒杀积分支付上限"> </el-table-column>
                </el-table>
              </div>
              <div class="btn-delete">
                <el-button type="primary" icon="el-icon-delete" size="small" @click="allRemoveGoods(tableIndex)"
                  >删除</el-button
                >
              </div>
              <el-pagination
                background
                @current-change="handleCurrentChangeObj"
                :current-page.sync="currentPageObj"
                :page-size="paramsObj.limit"
                layout="prev, pager, next, jumper"
                :total="totalObj"
              ></el-pagination>
            </div>
          </el-tab-pane>
        </el-tabs>
      </template>
      <span slot="footer" class="dialog-footer" v-show="tableIndex == 0">
        <el-button type="primary" @click="onSubmit" :loading="btnLoading">提 交</el-button>
        <el-button @click="quxiao">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import spikeListView from './child/spikeList'
import successSpikeListView from './child/successSpikeList'

export default {
  name: 'registrationDetails',
  components: { spikeListView, successSpikeListView },
  inject: ['reload'],
  data() {
    return {
      spilkeArr: [],
      dialogVisible: false,
      dialogLoading: false,
      activeName: 'All',
      selectedGoods: { index: 1, arr: [] },
      tableIndex: 0,
      closeModal: false,
      title: '已选中(0)',
      mark: '',
      id: null,
      editView: false,
      editLoading: false,
      form: {},
      currentPage: 1,
      total: 0,
      params: {
        signUpId: '',
        goodsName: '',
        goodsId: '',
        categories: [],
        page: 1,
        limit: 6
      },
      objSplike: {
        saleQuantity: [],
        shopPrice: [],
        cashPrice: [],
        huanbiPrice: [],
        max_quantity: [],
        maxQuantity: []
      },
      isReserve: true,
      loading: false,
      options: [],
      value: [],
      selectAll: [],
      goodsListArr: [],
      getRowKey(row) {
        return row.goodsId
      },
      currentPageObj: 1,
      totalObj: 0,
      paramsObj: {
        page: 1,
        limit: 6
      },
      isReserveObj: true,
      loadingObj: false,
      goodsListobj: [],
      goodsArrList: [],
      goodsSpikeArr: [],
      selectAllObj: [],
      selectAlls: [],
      getRowKeyObj(row) {
        return row.goodsId
      },
      btnLoading: false
    }
  },
  created() {
    // console.log(this.$route)
    this.mark = this.$route.query.mark
    this.id = this.$route.params.id
    this.getProductList(this.$route.params.id)
  },
  methods: {
    goodsInfo(e) {
      // console.log(e);
      this.form = e
      this.params.signUpId = e.id
      this.goodsList()
    },
    getProductList(n) {
      this.$api.get(`hd/itzSecbuySignup/${n}/category`, null, res => {
        if (res.status == 200) {
          this.options = res.data
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.goodsList()
    },
    handleCurrentChangeObj(currentPage) {
      this.paramsObj.page = currentPage
      console.log(this.goodsArrList)
      for (let i in this.goodsArrList) {
        if (Number(i) + 1 == currentPage) {
          console.log(i + 1)
          this.goodsSpikeArr = this.goodsArrList[i]
        }
      }
    },
    reSetData(dataList, num) {
      let arr = []
      let len = dataList.length
      for (let i = 0; i < len; i += num) {
        arr.push(dataList.slice(i, i + num))
      }
      return arr
    },
    search() {
      this.currentPage = 1
      this.params.page = 1
      this.goodsList()
    },
    clear() {
      this.currentPage = 1
      this.params.page = 1
      this.params.goodsName = ''
      this.params.goodsId = ''
      this.params.categories = ''
      this.value = []
      this.goodsList()
    },
    inputshopPrice(e) {
      if (this.objSplike.cashPrice[e]) {
        if (Number(this.objSplike.cashPrice[e]) > Number(this.objSplike.shopPrice[e])) {
          this.$message({
            message: `商品编码为${e}的商品，秒杀总价不能小于秒杀现金价！`,
            type: 'warning'
          })
          this.$set(this.objSplike.shopPrice, e, '')
          return
        }
        this.$set(
          this.objSplike.huanbiPrice,
          e,
          Number(this.objSplike.shopPrice[e]) - Number(this.objSplike.cashPrice[e])
        )
      } else {
        this.$set(this.objSplike.huanbiPrice, e, Number(this.objSplike.shopPrice[e]))
      }
    },
    inputCashPrice(e) {
      if (this.objSplike.shopPrice[e]) {
        if (Number(this.objSplike.cashPrice[e]) >= Number(this.objSplike.shopPrice[e])) {
          this.$message({
            message: `商品编码为${e}的商品，秒杀现金价需小于秒杀总价！`,
            type: 'warning'
          })
          this.$set(this.objSplike.cashPrice, e, '')
          return
        }
        this.$set(
          this.objSplike.huanbiPrice,
          e,
          Number(this.objSplike.shopPrice[e]) - Number(this.objSplike.cashPrice[e])
        )
      } else {
        this.$set(this.objSplike.huanbiPrice, e, Number(this.objSplike.cashPrice[e]))
      }
    },
    handleClick(tab) {
      this.tableIndex = tab.index
      if (tab.index == 1) {
        this.forEachArr(this.objSplike.saleQuantity, this.goodsListobj, 'surplusQuantity')
        this.forEachArr(this.objSplike.shopPrice, this.goodsListobj, 'signupShopPrice')
        this.forEachArr(this.objSplike.cashPrice, this.goodsListobj, 'signupCashPrice')
        this.forEachArr(this.objSplike.huanbiPrice, this.goodsListobj, 'signupMoneyLine')
        this.forEachArr(this.objSplike.maxQuantity, this.goodsListobj, 'maxQuantity')
      }
    },
    selectCall(selection) {
      // console.log(selection,'选择')
      // console.log(this.goodsListobj,'已选中')
      // console.log(this.objSplike)
      this.forEachArr(this.objSplike.saleQuantity, selection, 'surplusQuantity')
      this.forEachArr(this.objSplike.shopPrice, selection, 'signupShopPrice')
      this.forEachArr(this.objSplike.cashPrice, selection, 'signupCashPrice')
      this.forEachArr(this.objSplike.huanbiPrice, selection, 'signupMoneyLine')
      this.forEachArr(this.objSplike.maxQuantity, this.goodsListobj, 'maxQuantity')
      this.selectAll = selection
      this.title = `已选中(${selection.length})`
      this.goodsListobj = selection
      this.totalObj = this.goodsListobj.length
      this.goodsArrList = this.reSetData(this.goodsListobj, this.paramsObj.limit)
      for (let i in this.goodsArrList) {
        if (Number(i) + 1 == this.paramsObj.page) {
          this.goodsSpikeArr = this.goodsArrList[i]
        }
      }
    },
    goodsList() {
      this.params.categories = Array.from(this.value).toString()
      this.loading = true
      this.$api.get(
        `hd/itzSecbuySignupGoods/supplierGoods`,
        this.params,
        res => {
          // console.log(res);
          this.loading = false
          if (res.status == 200) {
            this.goodsListArr = res.data.rows
            this.total = res.data.total
          }
        },
        err => {
          this.loading = false
        }
      )
    },
    selectCallObj(selection) {
      // console.log(selection)
      this.selectAllObj = selection
    },
    allRemoveGoods(e) {
      console.log(e)
      if (e == 1) {
        let arr = this.selectAllObj,
          that = this
        for (let i of arr) {
          that.getIndex(that.goodsListobj, i)
        }
        let newArr = that.selectAllObj.filter(item => !that.goodsListobj.includes(item.goodsId))
        that.selectAllObj = that.goodsListobj
        this.title = `已选中(${that.selectAllObj.length})`
        this.selectAlls = newArr
        this.totalObj = this.goodsListobj.length
        this.goodsArrList = this.reSetData(this.goodsListobj, this.paramsObj.limit)
        // console.log(this.goodsArrList)
        // console.log(this.goodsListobj)
        this.paramsObj.page = 1
        this.currentPageObj = 1
        if (this.goodsArrList.length == 0) {
          this.goodsSpikeArr = []
        }
        for (let i in this.goodsArrList) {
          if (Number(i) + 1 == this.paramsObj.page) {
            console.log(this.goodsArrList[i], `tt`)
            this.goodsSpikeArr = this.goodsArrList[i]
          }
        }
      }
    },
    getIndex(_arr, _obj) {
      let length = _arr.length
      for (let i = 0; i < length; i++) {
        if (_arr[i] == _obj) {
          if (i == 0) {
            _arr.shift() //删除并返回数组的第一个元素
            return _arr
          } else if (i == length - 1) {
            _arr.pop() //删除并返回数组的最后一个元素
            return _arr
          } else {
            _arr.splice(i, 1) //删除下标为i的元素
            return _arr
          }
        }
      }
    },
    onSubmit() {
      this.forEachArr(this.objSplike.saleQuantity, this.goodsListobj, 'surplusQuantity')
      this.forEachArr(this.objSplike.shopPrice, this.goodsListobj, 'signupShopPrice')
      this.forEachArr(this.objSplike.cashPrice, this.goodsListobj, 'signupCashPrice')
      this.forEachArr(this.objSplike.huanbiPrice, this.goodsListobj, 'signupMoneyLine')
      this.forEachArr(this.objSplike.maxQuantity, this.goodsListobj, 'maxQuantity')
      let signupId = this.params.signUpId,
        saleQuantity = this.objSplike.saleQuantity,
        shopPrice = this.objSplike.shopPrice,
        cashPrice = this.objSplike.cashPrice,
        huanbiPrice = this.objSplike.huanbiPrice,
        maxQuantity = this.objSplike.maxQuantity,
        obj = {},
        spike = []
      for (let i in saleQuantity) {
        obj = {
          goodsId: i,
          signupId: signupId,
          saleQuantity: saleQuantity[i],
          shopPrice: shopPrice[i],
          cashPrice: cashPrice[i],
          huanbiPrice: huanbiPrice[i],
          maxQuantity: maxQuantity[i]
        }
        spike.push(obj)
      }
      for (let j in this.goodsArrList) {
        for (let i of this.goodsArrList[j]) {
          if (!i.surplusQuantity) {
            this.$message({
              message: `商品编码为${i.goodsId}的商品，秒杀库存不能为空！`,
              type: 'warning'
            })
            return
          }
          if (!i.signupShopPrice) {
            this.$message({
              message: `商品编码为${i.goodsId}的商品，秒杀总价不能为空！`,
              type: 'warning'
            })
            return
          }
          if (!i.signupCashPrice) {
            this.$message({
              message: `商品编码为${i.goodsId}的商品，秒杀现金价不能为空！`,
              type: 'warning'
            })
            return
          }
          // if (!i.signupMoneyLine) {
          //   this.$message({
          //     message: `商品编码为${i.goodsId}的商品，秒杀积分支付上限不能为空！`,
          //     type: 'warning'
          //   })
          //   return
          // }
        }
      }
      for (let i of spike) {
        for (let j of this.goodsListArr) {
          if (i.goodsId == j.goodsId) {
            if (j.goodsNumber < i.saleQuantity) {
              this.$message({
                message: `商品编码为${i.goodsId}的商品，秒杀库存不能大于商品库存！`,
                type: 'warning'
              })
              return
            }
            if (j.shopPrice < i.shopPrice) {
              this.$message({
                message: `商品编码为${i.goodsId}的商品，秒杀总价不能大于原商品总价！`,
                type: 'warning'
              })
              return
            }
            if (j.cashPrice < i.cashPrice) {
              this.$message({
                message: `商品编码为${i.goodsId}的商品，秒杀现金价不能大于原商品现金价！`,
                type: 'warning'
              })
              return
            }
            // if (j.moneyLine < i.huanbiPrice) {
            //   this.$message({
            //     message: `商品编码为${i.goodsId}的商品，秒杀积分支付上限不能大于原商品积分支付上限！`,
            //     type: 'warning'
            //   })
            //   return
            // }
          }
        }
      }
      this.btnLoading = true
      this.$api.post(
        `hd/itzSecbuySignupGoods/batch`,
        spike,
        res => {
          // console.log(res);
          this.btnLoading = false
          if (res.status == 200) {
            this.$message({
              message: res.message,
              type: 'success'
            })
            this.dialogVisible = false
            this.totalObj = 0
            this.goodsListobj = []
            this.goodsArrList = []
            this.goodsSpikeArr = []
            this.selectAllObj = []
            this.selectAlls = []
            this.selectAll = []
            this.objSplike.saleQuantity = []
            this.objSplike.shopPrice = []
            this.objSplike.cashPrice = []
            this.objSplike.huanbiPrice = []
            this.objSplike.maxQuantity = []
            this.title = `已选中(0)`
            this.$refs.multipleTable.clearSelection()
            this.goodsList()
            this.reload()
          }
        },
        err => {
          this.btnLoading = false
        }
      )
    },
    forEachArr(a, b, c) {
      for (let i in a) {
        for (let j of b) {
          if (j.goodsId == i) {
            j[c] = a[i]
            j.signupId = this.params.signUpId
          }
        }
      }
    },
    quxiao() {
      this.toggleSelection()
      this.dialogVisible = false
      this.objSplike.saleQuantity = []
      this.objSplike.shopPrice = []
      this.objSplike.cashPrice = []
      this.objSplike.huanbiPrice = []
      this.objSplike.maxQuantity = []
    },
    toggleSelection(rows, type) {
      if (rows) {
        if (type == 1) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row, false)
          })
        } else {
          rows.forEach(row => {
            this.$refs.objTable.toggleRowSelection(row, false)
          })
        }
      } else {
        this.$refs.multipleTable.clearSelection()
      }
      this.selectAlls = []
    }
  },
  watch: {
    tableIndex(val, oldVal) {
      if (val == 0) {
        console.log(this.selectAlls)
        this.toggleSelection(this.selectAlls, 1)
      } else {
        this.toggleSelection(this.goodsListArr, 2)
      }
    }
  }
}
</script>

<style scoped lang="scss">
/deep/.el-dialog {
  margin-top: 4vh !important;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.clearfix:after {
  display: block;
  clear: both;
  content: '';
  visibility: hidden;
  height: 0;
}
.btn-delete {
  float: left;
  margin: 10px 0 0;
}
/deep/ .el-breadcrumb {
  line-height: 34px;
}

/deep/ .el-tabs__item {
  padding: 0 100px;
}

/deep/ .el-tabs__item:nth-child(2) {
  padding: 0 100px !important;
}
/deep/.el-dialog__footer {
  text-align: center;
